<template>
  <div>
    <el-button style="width: 300px; margin-bottom: 10px" @click="dialogVisible = true"></el-button>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="90%">
      <span>{{aboutUs}}</span>
      <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="dialogVisible = false">OK</el-button>
    </span>
      </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'AboutUs',
  data () {
    return {
      aboutUs: '1983(NUS)(NTU) AMCISA',
      dialogVisible: false

    }
  }
}
</script>



